body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.circleBtn {
  height: 55px;
  width: 55px;
  border-radius: 50%;
  margin: 5px;
  border: none;
  background-color: rgb(11, 252, 31);
}

.msginp {
  width: 430px;
  height: 55px;
  border-radius: 50px 50px 50px 50px;
  margin-top: 5px;
  margin-left: 5px;
  border: none;
  padding-right: 10px;
  padding-left: 10px;
}

.cola {
  height: 65px;
  width: 65px;
  padding-left: 10px;
}
.msgalignend {
  display: flex;
  justify-content: flex-end;
}
.msgalignstart {
  display: flex;
  justify-content: flex-start;
  min-height: 5px;
}

.botmsg {
  background-color: rgb(205, 173, 241);
  padding-left: 15px;
  padding-right: 15px;
  height: auto;
  min-height: 45px;
  padding-top: 6px;
  padding-bottom: 4px;
  border-radius: 25px;
  max-width: 20rem;
}

.usermsg {
  background-color: rgb(235, 220, 252);
  padding-left: 15px;
  padding-right: 15px;
  height: auto;
  min-height: 45px;
  padding-top: 6px;
  padding-bottom: 4px;
  border-radius: 25px;
  max-width: 20rem;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(140, 27, 185);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b909e6;
}

.botIcon {
  font-size: 45px;
  border: 1px solid black;
  border-radius: 50px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 5px;
}

.allin {
  display: flex;
  justify-content: center;
  align-items: center;
}
.child {
  width: 40rem;
}
.userIcon {
  font-size: 40px;
  border: 1px solid black;
  border-radius: 50px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 5px;
}

.sendBtn {
  font-size: 25px;
}

